import React, { useState, useEffect } from "react";
import * as Yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import {
  Box,
  Card,
  FormControl,
  FormControlLabel,
  FormLabel,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  TextField,
  Typography,
} from "@material-ui/core";
import toast from "react-hot-toast";
import axios from "axios";
import { Field, Formik } from "formik";
import Save from "../Buttons/Save";
import ResetButton from "../Buttons/ResetButton";
import Design from "../Design";
import {useNavigate,useLocation } from "react-router-dom";
import {customAxios} from "../apis/Axios";
import { Section } from "../HelperFunction";
const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch",
      margin: "17px",
    },
  },
  typography: {
    padding: "17px",
  },
  head: {
    height: "120vh",
    paddingLeft: 30,
    paddingRight: 30,
    paddingTop: 10,
    background: "none",
  },
  highlight:
    theme.palette.type === "light"
      ? {
          color: theme.palette.secondary.dark,
          // backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          // backgroundColor: theme.palette.secondary.dark,
        },
}));

export default function AddClass() {
  const classes = useStyles();
  const Navigate = useNavigate();
  const { state } = useLocation();

  // console.log("state",state)


  if(state === null){
    var invoice= {
      name: "",
      classid: "",
      yearid: "",
      subject: [],
      subjectCode: "",
    };
  }else{
    state.invoice;
    var {invoice }= state;
  }
  const [classObj, setClassObj] = useState([]);
  const [subject, setSubject] = useState([]);
  const [Session, setSession] = useState([]);




  const classData = async () => {
    const fun = Section("class/api");
    fun.then((res) => {
      setClassObj(res);
    });
    const fun2 = Section("books/subjects");
    fun2.then((res) => {
      setSubject(res);
    });
    const fun3 = Section("class/session");
    fun3.then((res) => {
      setSession(res);
    });

  }
  useEffect(() => {
    classData();
  
  }, []);
  return (
    <>
      <Card className={classes.head}>
        <Formik
          // enableReinitialize
          initialValues={{
            name: invoice.name || "",
            classid: invoice.classid || "",
            subject: invoice.subject || [],
            yearid: invoice.yearid || "",
            subjectCode: invoice.subjectCode || "",
          }}
          validationSchema={Yup.object().shape({
            name: Yup.string().max(255).required("name is required"),
            // classteacher: Yup.string()
            //   .max(255)
            //   .required("Password is required"),
          })}
          onSubmit={async (values) => {
            // console.log("val", values);
            try {
              const data = { ...values, id: invoice.id ? invoice.id : "" };

              await new Promise((r) => setTimeout(r, 500));

              customAxios.postCall("books/subjectGroup",{...data}).then(res=>{

                if(res.status==200){
                  toast.success(res.data.message)
                }
                console.log(res)
              });
              // axios
              //   .post("http://localhost:8000/books/subjectGroup", {
              //     ...data,
              //   })
              //   .then((res) => {
              //     return res.json;
              //   });
            } catch (err) {
              console.log(err);
            }

            Navigate("../subjectGrouped_list");
          }}
        >
          {({
            errors,
            handleBlur,
            handleChange,
            handleSubmit,
            isSubmitting,
            dirty,
            touched,
            values,
            handleReset,
          }) => (
            <div>
              <Design
                text="Assaign Subject To Class"
                dashText="Assaign Subject To Class"
              />
              <Box style={{ paddingLeft: 20 }} boxShadow={20} color="black">
                <div className={classes.highlight}>
                  <Typography sx={{ padding: "17px" }} color="textPrimary" variant="h5">
                    Assaign Subject To Class
                  </Typography>
                </div>
                <form noValidate onSubmit={handleSubmit}>
                  <div>
                    <TextField
                      autoComplete="off"
                      id="name"
                      label="Subject Group Name"
                      style={{ margin: 8, width: "95%" }}
                      variant="outlined"
                      type="text"
                      error={Boolean(touched.name && errors.name)}
                      helperText={touched.name && errors.name}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.name}
                    />
                    <FormControl style={{ margin: 8, width: "95%" }}>
                      <InputLabel id="yearid">Year</InputLabel>

                      <Select
                        fullWidth
                        labelId="yearid"
                        value={values.yearid}
                        onChange={handleChange}
                        id="yearid"
                        label="Year"
                        name="yearid"
                        // multiple
                      >
                        {Session.map((option) => (
                          <MenuItem key={option.id} value={option.id}>
                            {option.year}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                    <FormControl style={{ margin: 8, width: "95%" }}>
                      <InputLabel id="classid">Class</InputLabel>

                      <Select
                        fullWidth
                        labelId="classid"
                        value={values.classid}
                        onChange={handleChange}
                        id="classid"
                        label="Year"
                        name="classid"
                        // multiple
                      >
                        {classObj.map((option) => (
                          <MenuItem key={option.id} value={option.id}>
                            {option.className}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                    <div
                      className={classes.highlight}
                      style={{ marginLeft: 10 }}
                      id="checkbox-group"
                    >
                      Select Sujects
                    </div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        marginLeft: 10,
                      }}
                      role="group"
                      aria-labelledby="checkbox-group"
                    >
                      {subject.map((user) => {
                        return (
                          <InputLabel>
                            <Field
                              type="checkbox"
                              name="subject"
                              key={user.id}
                              value={user.subjectName}
                            />
                            {user.subjectName}
                          </InputLabel>
                        );
                      })}
                      {/* <InputLabel>
                        <Field type="checkbox" name="subject" value="Urdu" />
                        Urdu
                      </InputLabel>
                      <InputLabel>
                        <Field type="checkbox" name="subject" value="Math" />
                        Math
                      </InputLabel>{" "}
                      <InputLabel>
                        <Field
                          type="checkbox"
                          name="subject"
                          value="islamiat"
                        />
                        islamiat
                      </InputLabel>{" "}
                      <InputLabel>
                        <Field type="checkbox" name="subject" value="science" />
                        science
                      </InputLabel>{" "}
                      <InputLabel>
                        <Field
                          type="checkbox"
                          name="subject"
                          value="pak-study"
                        />
                        pak-study
                      </InputLabel> */}
                    </div>

                    <TextField
                      id="subjectCode"
                      label="Subject Code"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      error={Boolean(touched.subjectCode && errors.subjectCode)}
                      helperText={touched.subjectCode && errors.subjectCode}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      margin="normal"
                      value={values.subjectCode}
                      variant="outlined"
                    />
                  </div>
                  <Save
                    text={invoice.id ? "update " : "Save"}
                    variant="contained"
                    size="medium"
                    color="success"
                    type="submit"
                    disabled={isSubmitting}
                  />

                  <ResetButton
                    text="Reset"
                    variant="contained"
                    size="medium"
                    color="primary"
                    type="button"
                    onClick={handleReset}
                    disabled={!dirty || isSubmitting}
                  />
                </form>
              </Box>
            </div>
          )}
        </Formik>
        <Typography style={{ marginTop: 20 }}>
          © Copyrights <b>akkhor</b> 2019. All rights reserved. Designed by
          PsdBosS
        </Typography>
      </Card>
    </>
  );
}
